import React from "react";
import { Col, Row, Select } from "antd";
import MaterialTable from 'material-table';

import { booking_history, earning_history_data } from "../data";

const { Option } = Select;
const columns = [
    { title: "Year", field: 'earningYear' },
    { title: "Month", field: 'earningMonth' },
    { title: "Gross Cost", field: 'earningGrossCost' },
    { title: "Customer Paid", field: 'earningCustomerPaid' }
];

class EarningReports extends React.Component {
    state = {};

    handleChange(value) {
        console.log(`selected ${value}`);
    }

    render() {
        return (
            <div>
                <Row>
                    <Col span={24}>
                        <h2 className="title gx-mb-4">Earning Report</h2>
                        <div style={{ marginBottom: "1%" }}>
                            <Select defaultValue="Select Option" style={{ width: '10%' }} onChange={this.handleChange}>
                                {booking_history.map((data) => (
                                    <Option value={data.name}>{data.name}</Option>
                                ))}
                            </Select>
                        </div>
                    </Col>
                    <Col span={24}>
                        <MaterialTable
                            title="Booking History"
                            columns={columns}
                            data={earning_history_data}
                            options={{
                                exportButton: true,
                                search: true
                            }}
                        />
                    </Col>
                </Row>
            </div>
        );
    }
}

export default EarningReports;
